body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.people {
  color: #2f4f4f;
  grid-template-columns: 200px 200px;
  justify-content: center;
  gap: 30px;
  margin-top: 30px;
  display: grid;
}

@media (width >= 750px) {
  .people {
    grid-template-columns: 200px 200px 200px 200px;
  }
}

.people :hover {
  border-radius: 25%;
  transition: all 1s;
  transform: scale(1.2);
}

.people :not(:hover) {
  transition: all 1s;
}

.people__person {
  text-align: center;
  transition: all 1s;
  position: relative;
}

.people__photo {
  border-radius: 50%;
  justify-content: center;
  width: 200px;
  height: 200px;
  display: flex;
  overflow: hidden;
}

.people__rank {
  color: red;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 37%;
  right: 10px;
}
